.footer {
  background: #5c5c5c;
  padding: 30px 0px 10px;
  font-size: 13px;
  margin-top: 50px; }
  .footer__link__block {
    display: flex;
    list-style: none;
    justify-content: center;
    flex-wrap: wrap; }
    .footer__link__block li {
      margin-bottom: 10px; }
    .footer__link__block a {
      color: #fff;
      margin-right: 50px; }
  .footer .first-block {
    justify-content: space-around;
    align-content: center; }
  .footer .first-block .fab {
    margin-right: 10px;
    font-size: 20px; }
  .footer .second-block a {
    position: relative; }
  .footer .second-block a::after {
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    content: "\f111";
    font-size: 5px;
    margin-left: 25px;
    position: absolute;
    top: 50%;
    right: 30;
    transform: translateY(-50%); }
  .footer .second-block li:last-child a::after {
    content: ''; }

@media (max-width: 400px) {
  .footer .second-block a::after {
    content: '';
    display: none; }
  .first-block {
    padding: 0px; }
  .second-block {
    flex-direction: column;
    align-content: center; } }
