.repair {
  margin: 60px 0px; }
  .repair h2 {
    text-align: center;
    font-size: 40px;
    font-weight: 700;
    margin-bottom: 40px; }
  .repair__buy {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    max-width: 900px;
    margin: 0 auto; }
    .repair__buy .buy__block {
      display: flex;
      flex-direction: column;
      flex-basis: 150px;
      margin: 2%;
      flex: 0 0 16%; }
  .repair a {
    display: inline-block;
    flex: 0 0 16%;
    padding: 100px 20px 20px 20px;
    color: #000;
    position: relative;
    text-align: center;
    font-weight: 900;
    transition: all, .5s;
    border-radius: 15px;
    background-color: #F3F3F3; }
    .repair a:hover {
      background-color: #fbfbfb;
      text-decoration: none; }
    .repair a img {
      position: absolute;
      top: 20px;
      left: 50%;
      transform: translateX(-50%); }

@media (max-width: 992px) {
  .repair h2 {
    font-size: 26px; }
  .repair__buy .buy__block {
    margin: 20px 0px;
    flex: 0 0 30%; } }

@media (max-width: 470px) {
  .repair__buy .buy__block {
    margin: 2%;
    flex: 0 0 46%; } }
