.header {
  margin: 22px 0px 0px 0px; }
  .header .container {
    padding-right: 10px;
    padding-left: 10px; }
  .header .row {
    align-items: center;
    justify-content: space-between; }
  .header__logo {
    flex: 0 0 12.5%;
    margin: 0px;
    padding: 0px;
    max-width: 11.5%; }
    .header__logo p {
      color: #fff;
      text-align: right;
      font-weight: 700;
      color: white;
      width: max-content;
      line-height: 1;
      padding: 5px 25px;
      background-color: #2B88CA; }
    .header__logo .uppercase {
      text-transform: uppercase;
      font-family: "PT Sans", Arial, sans-serif;
      font-size: 28px;
      font-weight: 700; }
  .header__description {
    font-size: 13px;
    font-family: "Roboto", Arial, sans-serif;
    font-weight: 400;
    line-height: 1.2;
    padding: 0px;
    margin: 0px;
    flex: 0 0 22%;
    max-width: 22%;
    padding-right: 20px; }
  .header__metrotel__block {
    position: relative;
    max-width: max-content;
    font-size: 16px;
    flex: 0 0 15%;
    max-width: 15%;
    margin: 0px;
    padding: 0px 5px 0px 30px; }
    .header__metrotel__block p:first-child {
      margin-bottom: 0px;
      font-weight: bold; }
    .header__metrotel__block p:last-child {
      font-size: 13px;
      font-weight: 300px; }
    .header__metrotel__block::before {
      content: 'M';
      position: absolute;
      top: 2px;
      left: 0px;
      border-radius: 50%;
      width: 20px;
      height: 20px;
      text-align: center;
      color: #fff;
      font-weight: 900;
      font-size: 12px;
      display: block;
      width: max-content;
      height: max-content;
      padding: 2px 6px; }
    .header__metrotel__block--red::before {
      background: #EF1E25; }
    .header__metrotel__block--pink::before {
      background: #B61D8E; }
    .header__metrotel__block--silver::before {
      background: #ACADAF; }

.menu__block__container {
  width: 100%;
  background: #2B88CA;
  margin: 0 auto; }
  .menu__block__container .navbar {
    max-width: 1200px;
    margin: 0px 20px;
    background: #2B88CA;
    color: #fff; }
    .menu__block__container .navbar .navbar-brand {
      color: #fff !important;
      display: none; }
    .menu__block__container .navbar .navbar-nav .nav-link {
      color: #fff;
      font-weight: 700; }
    .menu__block__container .navbar .form-inline .form-control {
      border-radius: 30px;
      width: 300px;
      text-align: center;
      color: #a3a3a3;
      font-size: 13px;
      border-style: solid;
      border-color: #67B4EB;
      border-width: 3px;
      line-height: 1.55;
      padding: 18px 0px;
      transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out, border-color 0.2s ease-in-out;
      margin-right: 30px !important; }
    .menu__block__container .navbar .form-inline .btn {
      display: none; }
    .menu__block__container .navbar .dropdown-menu {
      width: 1140px;
      padding: 15px 15px 0px 15px; }
      .menu__block__container .navbar .dropdown-menu .navbar-light .navbar-toggler {
        color: rgba(255, 255, 255, 0.5); }
      .menu__block__container .navbar .dropdown-menu__call__link {
        margin-top: 50px; }
        .menu__block__container .navbar .dropdown-menu__call__link a {
          background: #2B88CA;
          color: #fff;
          border-radius: 30px;
          padding: 15px 40px;
          font-weight: 900; }
          .menu__block__container .navbar .dropdown-menu__call__link a:hover {
            text-decoration: none; }
      .menu__block__container .navbar .dropdown-menu__link {
        background-color: #f3f3f3;
        display: inline-block;
        padding: 10px 0px;
        border-radius: 5px;
        width: 15%;
        font-weight: 700;
        color: #000;
        margin-bottom: 10px;
        margin-right: 5px;
        text-align: center;
        font-size: 16px; }
        .menu__block__container .navbar .dropdown-menu__link:hover {
          text-decoration: none; }
      .menu__block__container .navbar .dropdown-menu .row {
        align-items: flex-start; }
        .menu__block__container .navbar .dropdown-menu .row .col-md-8 {
          max-width: 60%; }
        .menu__block__container .navbar .dropdown-menu .row .col-md-4 .features {
          background: #2B88CA;
          position: absolute;
          top: -50px;
          left: -50px;
          display: block;
          border-radius: 50%;
          padding: 25px 10px;
          color: #fff;
          text-align: center;
          font-weight: bold;
          border-style: solid;
          border-color: #67B4EB;
          border-width: 5px; }
          .menu__block__container .navbar .dropdown-menu .row .col-md-4 .features p:first-child {
            line-height: 1;
            font-size: 24px;
            font-weight: 900;
            margin-bottom: 10px; }
          .menu__block__container .navbar .dropdown-menu .row .col-md-4 .features p:last-child {
            line-height: 1;
            margin-bottom: 0; }
      .menu__block__container .navbar .dropdown-menu__img {
        display: block;
        margin-left: auto;
        position: relative;
        width: 100%;
        height: auto; }

