.callback__description {
  background-image: url("../img/callback-bg.jpg");
  color: #fff;
  border-radius: 30px;
  padding: 30px; }
  .callback__description h2 {
    font-weight: 900;
    margin-bottom: 20px; }
  .callback__description p {
    font-size: 15px; }

.callback__form h2 {
  text-align: center;
  font-weight: bold;
  margin-bottom: 15px; }

.callback__form input, .callback__form select {
  border-radius: 30px;
  padding: 20px 15px;
  height: auto;
  font-size: 13px;
  border: 3px solid #f3f3f3;
  margin-bottom: 20px; }

.callback__form .input-tel {
  font-size: 15px; }

.callback__form .btn {
  color: #fff;
  font-weight: bold;
  font-size: 14px;
  display: block;
  width: 100%;
  text-align: block;
  border-radius: 30px;
  padding: 15px;
  background-color: #2B88CA; }

.callback__form select option:checked,
.callback__form select option:hover {
  box-shadow: 0 0 10px 100px #000 inset; }

.callback__form select:focus > option:hover {
  background: #000 !important; }
