.header__text__block {
  background-color: #F3F3F3;
  font-family: "Roboto", Arial, sans-serif;
  color: #fff;
  margin: 0 auto; }
  .header__text__block h1 {
    margin-top: 0;
    margin-bottom: 20px;
    line-height: 1;
    font-weight: 900;
    font-family: "PT Sans", Arial, sans-serif;
    font-size: 40px; }
  .header__text__block__bg {
    max-width: 1500px;
    background: url("../img/text-block-bg.jpg") center center;
    margin: 0 auto; }
  .header__text__block .container {
    padding-top: 60px; }
  .header__text__block .img-block {
    align-self: flex-end; }
  .header__text__block img {
    display: block;
    width: 100%; }
  .header__text__block__description {
    font-size: 15px;
    font-weight: 400; }
  .header__text__block__features {
    display: flex; }
    .header__text__block__features .features {
      flex: 0 0 33%;
      text-align: center;
      justify-content: space-between;
      margin: 20px 0px; }
      .header__text__block__features .features p:first-child {
        font-size: 15px;
        font-weight: 900;
        margin-bottom: 0; }
      .header__text__block__features .features p:last-child {
        font-size: 24px;
        font-weight: 900;
        font-family: "PT Sans", Arial, sans-serif; }
  .header__text__block__callbtn a {
    color: #fff;
    display: inline-block;
    background-color: #2B88CA;
    background: #2B88CA;
    border-radius: 30px;
    padding: 15px 40px;
    font-weight: 900; }

@media (max-width: 1200px) {
  .header__text__block__callbtn {
    margin-bottom: 20px; } }

@media (max-width: 600px) {
  .header__text__block__callbtn {
    text-align: center; } }

@media (max-width: 430px) {
  .header__text__block__features {
    flex-direction: column; } }
